import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";

import logo_white from "../../app-assets/images/logo/logo-white.png";
import icon_user from "../../app-assets/images/icon/icon_user.png";

import { useTranslation } from "react-i18next";

// HELPERS
import { routeMap } from "../../routes";

export const Header2 = () => {
  const { t } = useTranslation();

  const session = useSelector((state) => state?.session);
  const cur_loc = window.location.pathname;

  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 100);
    });
  }, []);

  const [menuToggle, setMenuToggle] = useState(false);
  const handleClick = () => {
    setMenuToggle(!menuToggle);
  };

  return (
    <header
      className="header1"
      style={
        scroll
          ? { backgroundColor: "rgba(0,0,0,0.75)" }
          : { backgroundColor: "transparent" }
      }
    >
      <div className="inner clear">
        <h1>
          <Link to="/">
            <img src={logo_white} alt-="CoinTC" />
          </Link>
        </h1>
        <nav>
          <h2 className="hidden">메인메뉴</h2>
          <div className="gnb_pc clear">
            <ul className="clear">
              <li
                className={
                  cur_loc === "/offer" ||
                  cur_loc === "/offer/create" ||
                  cur_loc === "/ad_payment_method" ||
                  cur_loc === "/ad_contract"
                    ? "on"
                    : ""
                }
              >
                <Link to="/offer">{t("P2P Trade")}</Link>
              </li>
              <li className={cur_loc === "/order" ? "on" : ""}>
                <Link to="/order">{t("Orders")}</Link>
              </li>
              <li className={cur_loc === "/wallet" ? "on" : ""}>
                <Link to="/wallet">{t("Wallet")}</Link>
              </li>
              <li className={cur_loc === "/affiliate" ? "on" : ""}>
                <Link to="/affiliate">{t("Affiliate")}</Link>
              </li>
              <li className={cur_loc === "/support" ? "on" : ""}>
                <Link to="/support">{t("Support")}</Link>
              </li>
            </ul>
          </div>

          {session?.user ? (
            <div className="user clear">
              <Link to="/me">
                <img src={icon_user} alt="My page" />
                <p>{t("My page")}</p>
              </Link>
            </div>
          ) : (
            <div className="user_btn clear">
              <Link
                to={routeMap?.login}
                className="btn btn_login text-white mr-3"
              >
                {t("Login")}
              </Link>
              <Link to={routeMap?.register} className="btn btn_signup">
                {t("Sign Up")}
              </Link>
            </div>
          )}
          <div
            className="side_menu"
            style={menuToggle ? { right: "0px" } : { right: "-250px" }}
          >
            <div className="burger_box">
              <div className="menu-icon-container">
                <Link
                  to="#"
                  className={
                    "menu-icon js-menu_toggle " +
                    (menuToggle ? "opened" : "closed")
                  }
                  onClick={handleClick}
                >
                  <span className="menu-icon_box">
                    <span className="menu-icon_line menu-icon_line--1"></span>
                    <span className="menu-icon_line menu-icon_line--2"></span>
                    <span className="menu-icon_line menu-icon_line--3"></span>
                  </span>
                </Link>
              </div>
            </div>
            <div className="user_m">
              <Link to={routeMap?.me}>
                <img src={icon_user} alt="My page" />
                <p>{t("My page")}</p>
              </Link>
            </div>
            <div className="gnb_m">
              <ul className="clear">
                <li>
                  <Link to={routeMap?.advert}>P2P Trade</Link>
                </li>
                <li>
                  <Link to={routeMap?.order}>Orders</Link>
                </li>
                <li>
                  <Link to={routeMap?.wallet}>Wallet</Link>
                </li>
                <li>
                  <Link to={routeMap?.affiliate}>Affiliate</Link>
                </li>
                <li>
                  <Link to={routeMap?.support}>Support</Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </header>
  );
};
