import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { Formik } from "formik";

import _constants from "../../../../_constants";
import useQuery from "../../../../_hooks/query.hook";
import { useTranslation } from "react-i18next";
import { routeMap } from "../../routes";

// HOOKS
import useServiceContextHook from "../../../../_hooks/service.context.hook";
import AuthForm from "../../components/form/auth.form";

export default function () {
  const { session, history } = useServiceContextHook();

  const query = useQuery();

  useEffect(() => {
    if (session?.user) history.push(routeMap?.home);
  }, [session]);

  useEffect(() => {
    if (query && query?.email) setShowFeedback(true);
  }, [query]);

  const [showFeedback, setShowFeedback] = useState(false);

  return showFeedback ? <Feedback /> : <AuthForm.Register />;
}

function Feedback() {
  const {
    services: { auth },
  } = useServiceContextHook();
  const { t } = useTranslation();

  const query = useQuery();

  async function resendConfirmationMail() {
    return await auth.resendConfirmationMail({
      email: query?.email,
    });
  }

  return (
    <div
      style={{
        minHeight: "50vh",
        padding: 10,
        marginTop: 100,
        display: "flex",
        justifyContent: "center",
      }}
    >
      <header className="text-center" style={{ position: "relative" }}>
        <h2 className="h3 font-weight-bold">{t("Confirm Account")}</h2>
        <p>
          {t(
            "Confirm your account using the confirmation link sent to your registered email"
          )}
        </p>
        <button onClick={() => resendConfirmationMail(query?.email)}>
          {t("Resend email")}
        </button>
      </header>
    </div>
  );
}
