function PhoneVerificationSetting() {
  const { session } = useServiceContextHook();
  const { t } = useTranslation();
  const { toggledPayload, onOpen, onClose, isOpen } = useToggler();

  function ConfirmRemove() {
    return <>Are you sure?</>;
  }
  function useFormRenderer(data) {
    let { type, formData } = data;
    const determine = function () {
      switch (type) {
        case "remove": {
          return ["Remove Phone Number", <ConfirmRemove />];
        }
        default: {
          return [
            "Phone Number Verification",
            <KycForm.VerifyPhone initialValues={formData} />,
          ];
        }
      }
    };
    return determine();
  }

  return (
    <>
      <img src={icon_phone} alt="Phone Number Verification" />
      <dl>
        <dt>{t("Phone Number Verification")}</dt>
        <dd>{t("Protect your account and transactions.")}</dd>
      </dl>
      <p>
        <i className="fas fa-check-circle"></i>010****1234
      </p>
      {/* Change button */}
      <button
        type="button"
        onClick={() =>
          onOpen({
            type: "change",
            formData: { phone: session?.user?.profile?.phone },
          })
        }
        className="btn btn_change"
      >
        {t("Change")}
      </button>
      {/* Remove button */}
      <button
        type="button"
        onClick={() =>
          onOpen({
            type: "remove",
            formData: {},
          })
        }
        className="btn btn_remove"
      >
        {t("Remove")}
      </button>
      <ModalForm
        {...{ useFormRenderer, isOpen, onClose, formData: toggledPayload }}
      />
    </>
  );
}

